.header {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: rgb(210, 232, 255);
  position: relative;
  z-index: 1;
  display: flex;
}

.header-logo {
  height: 40px;
  width: 40px;
  padding: 10px;
}

.header-title {
  display: inline-block;
  line-height: 60px;
  font-size: 24px;
  font-weight: bold;
  color: #444;
}

.header-menu {
  position: absolute;
  right: 0px;
  display: inline-block;
  line-height: 50px;
  width: 50px;
  margin: 5px;
  font-size: 30px;
  font-weight: bold;
  color: #444;
  text-align: center;
  border-left: 1px solid rgb(199, 199, 199);
  cursor: pointer;
  user-select: none;
}